import React from 'react'
import NavBar from '../components/NavBar'
import Landing from '../components/Landing'
import Features from '../components/Features'
import MenuItems from '../components/MenuItems'
import About from '../components/About'
import Data from '../components/Data'
import Services from '../components/Services'
import Pricing from '../components/Pricing'
import Quotes from '../components/Quotes'
import ContactDet from '../components/ContactDet'
import LoginReg from '../components/LoginReg'




class Home extends React.Component {



    toggleMenu() {
        this.setState(() => {
            if (this.state.menuItem)
                return { menuItem: false }

            else return { menuItem: true }
        })
    }
    toggleLogin() {
        this.setState(() => {

            return { loginPage: true }
        })
    }
    gotohome() {
        this.setState(() => {

            return { loginPage: false }
        })
    }


    //   user Define function 

    constructor(props) {

        super(props)
        this.toggleMenu = this.toggleMenu.bind(this)
        this.toggleLogin = this.toggleLogin.bind(this)
        this.gotohome = this.gotohome.bind(this)




        this.state = {
            menuItem: false,
            loginPage: false
        }





    }

    render() {









        return (
            <div>
                {this.state.loginPage ? < LoginReg gotohome={this.gotohome} /> : null}
                <MenuItems isOn={this.state.menuItem ? { transform: "translateY(0px)" } : { transform: "translateY(-200vh)" }} toggleLogin={this.toggleLogin} />
                <NavBar toggleMenu={this.toggleMenu} btn={this.state.menuItem ? "cross" : "bar"} />
                <Landing />
                <Features />
                <About />
                <Data />
                <Services />
                <Pricing />
                <Quotes />
                <ContactDet />




            </div>
        )
    }

    componentDidMount() {
        if (this.state.menuItem) {
            document.getElementsByTagName('body')[0].style.overflow = "hidden"
        }
        else {
            document.getElementsByTagName('body')[0].style.overflow = "scroll"
        }
    }
}

export default Home