import React, { Component } from 'react';
import lod from '../img/svg/refresh.svg';
import cross from '../img/svg/cancel.svg';
import { GoogleLogin } from 'react-google-login';

class LoginReg extends Component {
	sendLogData() {
		this.setState(() => {
			return {
				loading: true
			};
		});
		let username = document.getElementById('username').value;
		let pass = document.getElementById('pass').value;

		fetch('/api/login', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({
				username: username,
				password: pass
			})
		})
			.then((res) => res.json())
			.then((parJson) => {
				if (parJson.username === username) {
					window.location.href = '/main';
				} else if (parJson.error) {
					alert(parJson.error);

					this.setState(() => {
						return {
							loading: false
						};
					});
				}
			})
			.catch((error) => {
				alert(error);
				this.setState(() => {
					return {
						loading: false
					};
				});
			});
	}

	sendRegData() {
		this.setState(() => {
			return {
				loading: true
			};
		});

		let email = document.getElementById('email').value;
		let pass = document.getElementById('pass').value;
		let name = document.getElementById('name').value;
		let username = document.getElementById('username').value;
		let occupation = document.getElementById('occupation').value;
		let phone_num = document.getElementById('phone_num').value;

		if (email.indexOf('@') === -1 || email.indexOf('.') === -1) {
			alert('Invalid email ');
			return;
		}

		if (pass.length < 8) {
			alert('Password should be greater than 8 letters');
			return;
		}

		fetch('/api/register', {
			method: 'POST',
			headers: { 'Content-Type': 'application/json' },
			body: JSON.stringify({
				user: {
					username: username,
					email: email,
					password: pass,
					phone_num: phone_num,
					occupation: occupation,
					full_name: name
				}
			})
		})
			.then((res) => res.json())
			.then((parJson) => {
				if (parJson.user.token) {
					this.setlog();
					alert('Registration Complete Now log in ');
					pass = '';

					this.setState(() => {
						return {
							loading: false
						};
					});
				} else if (parJson.error) {
					alert(parJson.error);
					this.setState(() => {
						return {
							loading: false
						};
					});
				}
			})
			.catch((error) => {
				alert(error);
				this.setState(() => {
					return {
						loading: false
					};
				});
			});
	}

	setreg() {
		this.setState(() => {
			return { loc: 'reg' };
		});
	}
	setlog() {
		this.setState(() => {
			return { loc: 'log' };
		});
	}

	constructor(props) {
		super(props);

		this.setreg = this.setreg.bind(this);
		this.setlog = this.setlog.bind(this);
		this.sendLogData = this.sendLogData.bind(this);
		this.sendRegData = this.sendRegData.bind(this);
		this.state = {
			loc: 'log',
			loading: false
		};
	}

	render() {
		const responseGoogle = (response) => {
			fetch('/api/register/google', {
				method: 'POST',
				headers: { 'Content-Type': 'application/json' },
				body: JSON.stringify({ tokenId: response.tokenId })
			})
				.then((res) => res.json())
				.then((parJson) => {
					if (parJson.email) {
						window.location.href = '/main';
					} else if (parJson.error) {
						this.setState(() => {
							return {
								loading: false
							};
						});
					}
				})
				.catch((error) => {
					alert(error);
					this.setState(() => {
						return {
							loading: false
						};
					});
				});
		};

		const responseGoogleError = (response) => {
			console.log(response);
			alert('Unable to login with Google Please try again with different method');
		};

		return (
			<div>
				<div className="overlay" onClick={this.props.remLogReg} />

				<div className="logReg">
					<div className="login_head">
						<h1>Accute Accountings</h1>
						<img className="gotohomebtn" onClick={this.props.gotohome} src={cross} alt="" />
					</div>

					<div className="login_body">
						<div className="login_body_left">
							<div className="logNav">
								<span onClick={this.setlog} className={this.state.loc === 'log' ? 'active log' : 'log'}>
									Login
								</span>
								<span
									onClick={this.setreg}
									className={this.state.loc === 'reg' ? 'active reg ' : 'reg'}
								>
									Register
								</span>
							</div>

							{this.state.loc === 'log' ? null : <input id="name" type="text" placeholder="Name" />}
							{this.state.loc === 'log' ? null : <input id="email" type="text" placeholder="Email" />}

							{this.state.loc === 'log' ? null : (
								<input id="phone_num" type="text" placeholder="Phone Number" />
							)}
							{this.state.loc === 'log' ? null : (
								<input id="occupation" type="text" placeholder="Occupation" />
							)}

							<input id="username" type="text" placeholder="Username" />

							<input id="pass" type="password" placeholder="Password" />

							{this.state.loc === 'log' ? (
								<button className="loginBtn btnbtn" onClick={this.sendLogData}>
									{this.state.loading ? <img className="loadingsvg" src={lod} /> : 'Login'}
								</button>
							) : (
								<button className="regBtn btnbtn" onClick={this.sendRegData}>
									{' '}
									{this.state.loading ? <img className="loadingsvg" src={lod} /> : 'Register'}
								</button>
							)}
						</div>

						<div className="login_body_right">
							<GoogleLogin
								clientId="462910295856-qu6jggc7h5mgei80onc32ehhjfiikhsh.apps.googleusercontent.com"
								render={(renderProps) => (
									<button
										type="button"
										onClick={renderProps.onClick}
										disabled={renderProps.disabled}
										className="google-button"
									>
										<span class="google-button__icon">
											<svg
												id="google_logo"
												viewBox="0 0 366 372"
												xmlns="http://www.w3.org/2000/svg"
											>
												<path
													d="M125.9 10.2c40.2-13.9 85.3-13.6 125.3 1.1 22.2 8.2 42.5 21 59.9 37.1-5.8 6.3-12.1 12.2-18.1 18.3l-34.2 34.2c-11.3-10.8-25.1-19-40.1-23.6-17.6-5.3-36.6-6.1-54.6-2.2-21 4.5-40.5 15.5-55.6 30.9-12.2 12.3-21.4 27.5-27 43.9-20.3-15.8-40.6-31.5-61-47.3 21.5-43 60.1-76.9 105.4-92.4z"
													id="Shape"
													fill="#EA4335"
												/>
												<path
													d="M20.6 102.4c20.3 15.8 40.6 31.5 61 47.3-8 23.3-8 49.2 0 72.4-20.3 15.8-40.6 31.6-60.9 47.3C1.9 232.7-3.8 189.6 4.4 149.2c3.3-16.2 8.7-32 16.2-46.8z"
													id="Shape"
													fill="#FBBC05"
												/>
												<path
													d="M361.7 151.1c5.8 32.7 4.5 66.8-4.7 98.8-8.5 29.3-24.6 56.5-47.1 77.2l-59.1-45.9c19.5-13.1 33.3-34.3 37.2-57.5H186.6c.1-24.2.1-48.4.1-72.6h175z"
													id="Shape"
													fill="#4285F4"
												/>
												<path
													d="M81.4 222.2c7.8 22.9 22.8 43.2 42.6 57.1 12.4 8.7 26.6 14.9 41.4 17.9 14.6 3 29.7 2.6 44.4.1 14.6-2.6 28.7-7.9 41-16.2l59.1 45.9c-21.3 19.7-48 33.1-76.2 39.6-31.2 7.1-64.2 7.3-95.2-1-24.6-6.5-47.7-18.2-67.6-34.1-20.9-16.6-38.3-38-50.4-62 20.3-15.7 40.6-31.5 60.9-47.3z"
													fill="#34A853"
												/>
											</svg>
										</span>
										<span class="google-button__text">Sign in with Google</span>
									</button>
								)}
								buttonText="Login"
								onSuccess={responseGoogle}
								onFailure={responseGoogleError}
								cookiePolicy={'single_host_origin'}
							/>
							<GoogleLogin
								clientId="462910295856-qu6jggc7h5mgei80onc32ehhjfiikhsh.apps.googleusercontent.com"
								render={(renderProps) => (
									<button
										type="button"
										onClick={renderProps.onClick}
										disabled={renderProps.disabled}
										className="google-button"
									>
										<span class="google-button__icon">
											<svg
												id="google_logo"
												viewBox="0 0 366 372"
												xmlns="http://www.w3.org/2000/svg"
											>
												<path
													d="M125.9 10.2c40.2-13.9 85.3-13.6 125.3 1.1 22.2 8.2 42.5 21 59.9 37.1-5.8 6.3-12.1 12.2-18.1 18.3l-34.2 34.2c-11.3-10.8-25.1-19-40.1-23.6-17.6-5.3-36.6-6.1-54.6-2.2-21 4.5-40.5 15.5-55.6 30.9-12.2 12.3-21.4 27.5-27 43.9-20.3-15.8-40.6-31.5-61-47.3 21.5-43 60.1-76.9 105.4-92.4z"
													id="Shape"
													fill="#EA4335"
												/>
												<path
													d="M20.6 102.4c20.3 15.8 40.6 31.5 61 47.3-8 23.3-8 49.2 0 72.4-20.3 15.8-40.6 31.6-60.9 47.3C1.9 232.7-3.8 189.6 4.4 149.2c3.3-16.2 8.7-32 16.2-46.8z"
													id="Shape"
													fill="#FBBC05"
												/>
												<path
													d="M361.7 151.1c5.8 32.7 4.5 66.8-4.7 98.8-8.5 29.3-24.6 56.5-47.1 77.2l-59.1-45.9c19.5-13.1 33.3-34.3 37.2-57.5H186.6c.1-24.2.1-48.4.1-72.6h175z"
													id="Shape"
													fill="#4285F4"
												/>
												<path
													d="M81.4 222.2c7.8 22.9 22.8 43.2 42.6 57.1 12.4 8.7 26.6 14.9 41.4 17.9 14.6 3 29.7 2.6 44.4.1 14.6-2.6 28.7-7.9 41-16.2l59.1 45.9c-21.3 19.7-48 33.1-76.2 39.6-31.2 7.1-64.2 7.3-95.2-1-24.6-6.5-47.7-18.2-67.6-34.1-20.9-16.6-38.3-38-50.4-62 20.3-15.7 40.6-31.5 60.9-47.3z"
													fill="#34A853"
												/>
											</svg>
										</span>
										<span class="google-button__text">Sign in with Google</span>
									</button>
								)}
								buttonText="Login"
								onSuccess={responseGoogle}
								onFailure={responseGoogleError}
								cookiePolicy={'single_host_origin'}
							/>
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default LoginReg;
